import React from 'react';

interface ButtonGroupProps {
    onButtonClick: (action: string) => void;
    isLocationPinned: boolean;
}

const ButtonGroup: React.FC<ButtonGroupProps> = ({ onButtonClick, isLocationPinned }) => {
    if (!isLocationPinned) {
        return null;
    }

    return (
        <div>
            <button onClick={() => onButtonClick('action1')}>Action 1</button>
            <button onClick={() => onButtonClick('action2')}>Action 2</button>
            <button onClick={() => onButtonClick('action3')}>Action 3</button>
        </div>
    );
};

export default ButtonGroup;